import React from 'react';
import * as Utils from './dataProcessingUtils'

class Density_Map_2D_Card extends React.Component {

  /**
   * density
   * 
   */

  constructor( props ) {

    super( props );

    this.state = {
      mode: 'overview'
    }

    this.props.data.information.settings = {
      density: this.props.data.information.settings.density ?? 50,
      densityLat: this.props.data.information.settings.densityLat ?? 50,
      densityLong: this.props.data.information.settings.densityLong ?? 50,
      latMin: this.props.data.information.settings.latMin ?? null,
      latMax: this.props.data.information.settings.latMax ?? null,
      longMin: this.props.data.information.settings.longMin ?? null,
      longMax: this.props.data.information.settings.longMax ?? null,
      kernelSize: this.props.data.information.settings.kernelSize ?? 11,
      computeDirection: this.props.data.information.settings.computeDirection ?? false,
      convertTo3DMap: this.props.data.information.settings.convertTo3DMap ?? false
    };
    this.props.updateData( this.props.id, this.props.data );

  }

  handleClick = () => {

    let mode = this.state.mode === 'overview' ? 'modification' : 'overview';
    this.setState( { ...this.state, mode: mode } );

  }

  handleDelete = (event) => {

    event.stopPropagation();
    this.props.deleteData( this.props.id );

  }
  
  handleNameChange = (event) => {

    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.props.data[ name ] = value;

    this.props.updateData( this.props.id, this.props.data );

  }

  handleSettingsChange = (event) => {

    const target = event.target;
    let value = target.type === 'checkbox' ? target.checked : target.value;

    if ( value === '' || value === NaN ) {

      value = null;

    } else if ( target.type === 'number' ) {

      value = parseFloat(value);
      if ( target.max ) value = Math.min( value, target.max );
      if ( target.min ) value = Math.max( value, target.min );

    }

    const name = target.name;
    this.props.data.information.settings[name] = value;
    this.props.updateData( this.props.id, this.props.data );

  }

  handleChangeInit = (event) => {

    this.props.data.information.data.init = event.target.value;
    this.props.updateData( this.props.id, this.props.data );

  }

  addDataToAddList() {

    let selector = document.getElementById( "add_input_select_card_id_" + this.props.data.id );
    this.props.data.information.data.add = this.props.data.information.data.add ?? [];
    this.props.data.information.data.add.push( selector.value );
    this.props.updateData( this.props.id, this.props.data );

  }

  removeDataToAddList = ( event ) => {
    
    this.props.data.information.data.add.splice( event.target.dataset.index, 1 );
    this.props.updateData( this.props.id, this.props.data );

  }

  addDataToSubList() {

    let selector = document.getElementById( "sub_input_select_card_id_" + this.props.data.id );
    this.props.data.information.data.sub = this.props.data.information.data.sub ?? [];
    this.props.data.information.data.sub.push( selector.value );
    this.props.updateData( this.props.id, this.props.data );

  }

  removeDataToSubList = ( event ) => {
    
    this.props.data.information.data.sub.splice( event.target.dataset.index, 1 );
    this.props.updateData( this.props.id, this.props.data );

  }

  displayAsList( mylist, onClickFct ) {

    if ( !(mylist != null && Symbol.iterator in Object(mylist)) ) return '';

    let list = [];
    let i = 0;
    for ( let item of mylist ) {

      list.push(
        <span
          key={ i }
          data-index= {i}
          title={ item }
          onClick={ (e) => onClickFct(e) }>
          {this.truncate(item,12)}
        </span>
      );
      i++;

    }
    return list;

  }

  dataSelectOption() {

    let listOption = [];

    for ( let item of this.props.dataNameList ) {

      listOption.push(
        <option
          key={Math.floor( Math.random()*10000)}
          title={ item.name }
          value={ item.name }>
          { item.name }
        </option> 
      );

    }
    
    return listOption;

  }
  
  displayNullValue( valueToDisplay ) {

    return valueToDisplay === null ? '' : valueToDisplay;

  }

  truncate(str,n) {
    return (str.length > n) ? str.slice(0, n) + '...' : str;
  };


  render() {

    let content;

    if ( this.state.mode === 'overview' ) {
        
      content = <div className='card_content'>
        <div className='card_content_left'>
          <span>{ this.props.data.name }</span>
          <span>{ this.props.data.information.type }</span>
        </div>
        <div className='card_content_right'>
          <button className='icon_button' id='delete' onClick={this.handleDelete}></button>
        </div>
      </div>

    } else {

      content = <div className='card_content'>
        <div className='card_content_left'>
          <label id="name">
            Nom :
            <input
              name="name"
              value={this.props.data.name}
              onChange={this.handleNameChange}
            />
          </label>
          { Utils.TypeInput( this.props ) }
          <div className='left'>
            {/* <span>Donnes</span> */}
            <span className="init">
              Initialiser :
            </span>
            <select className="initInput" name="init" value={this.props.data.information.data.init}  onChange={this.handleChangeInit}>
              {this.dataSelectOption()}
            </select>
            <span className="add">
              Additionner :
            </span>
            <div className="addArea">
              {this.displayAsList( this.props.data.information.data.add, this.removeDataToAddList )}
            </div>
            <select className="addInput" id={"add_input_select_card_id_" + this.props.data.id}>
              {this.dataSelectOption()}
            </select>
            <button className="addBtn" id="add" onClick={() => this.addDataToAddList()}>+</button>
            <span className="sub">
              Soustraire :
            </span>
            <div className="subArea">
              {this.displayAsList( this.props.data.information.data.sub, this.removeDataToSubList )}
            </div>
            <select className="subInput" id={"sub_input_select_card_id_" + this.props.data.id}>
              {this.dataSelectOption()}
            </select>
            <button className="subBtn" id="add" onClick={() => this.addDataToSubList()}>+</button>
          </div>
          <div className='right'>
            <span>Proprits</span>
            <span>Densit</span>
            <div className="density">
              <label className='label_secondary'>
                <input
                  name="densityLat"
                  type="number"
                  min="1"
                  value={ this.displayNullValue( this.props.data.information.settings.densityLat ) }
                  onChange={this.handleSettingsChange}
                />
                <span>Lat</span>
              </label>
              <label className='label_secondary'>
                <input
                  name="densityLong"
                  type="number"
                  min="1"
                  value={ this.displayNullValue( this.props.data.information.settings.densityLong ) }
                  onChange={this.handleSettingsChange}
                />
                <span>Long</span>
              </label>
              <label className='label_secondary'>
                <input
                  name="kernelSize"
                  type="number"
                  min="1"
                  value={ this.displayNullValue( this.props.data.information.settings.kernelSize ) }
                  onChange={this.handleSettingsChange}
                />
                <span>Kernel</span>
              </label>
            </div>
            <span>Latitude</span>
            <div className="latitude_limits">
              <label className='label_secondary'>
                <input
                  name="latMin"
                  type="number"
                  min="-90"
                  max="90"
                  step="0.0001"
                  value={ this.displayNullValue( this.props.data.information.settings.latMin ) }
                  onChange={this.handleSettingsChange}
                />
                <span>min</span>
              </label>
              <span></span>
              <label className='label_secondary'>
                <input
                  name="latMax"
                  type="number"
                  min="-90"
                  max="90"
                  step="0.0001"
                  value={ this.displayNullValue( this.props.data.information.settings.latMax ) }
                  onChange={this.handleSettingsChange}
                />
                <span>max</span>
              </label>
            </div>
            <span>Longitude</span>
            <div className="longitude_limits">
              <label className='label_secondary'>
                <input
                  name="longMin"
                  type="number"
                  min="-180"
                  max="180"
                  step="0.0001"
                  value={ this.displayNullValue( this.props.data.information.settings.longMin ) }
                  onChange={this.handleSettingsChange}
                />
                <span>min</span>
              </label>
              <span></span>
              <label className='label_secondary'>
                <input
                  name="longMax"
                  type="number"
                  min="-180"
                  max="180"
                  step="0.0001"
                  value={ this.displayNullValue( this.props.data.information.settings.longMax ) }
                  onChange={this.handleSettingsChange}
                />
                <span>max</span>
              </label>
            </div>
            <div className='convertTo3DMap'>
              <input
                type="checkbox"
                id="convertTo3DMap"
                name="convertTo3DMap"
                value={this.props.data.information.settings.convertTo3DMap}
                onChange={this.handleSettingsChange}
              />
              <label htmlFor="convertTo3DMap">Convertir en 3D</label>
            </div>
          </div>
        </div>
        <div className='card_content_right'>
          <button className='icon_button' id='delete' onClick={this.handleDelete}></button>
          <button className='icon_button' id='collapse' onClick={this.handleClick}></button>
        </div>
      </div>

    }

    return (
      <div className={'card card_dataP_2D ' + ( this.state.mode ) }
        onClick={ this.state.mode === 'overview' ? this.handleClick : undefined }>
        {content}
      </div>
    )

  }

}


export default Density_Map_2D_Card;