import React from 'react';
import * as Inputs from '../cardInputs.js';
import objDesc from '../objectsDescription.json';

class ActionCard extends React.Component {

    constructor( props ) {

        super( props );

        this.state = {
            mode: 'overview'
        }

        // this.fillObjectProperties();
        this.props.updateData( this.props.id, this.props.data );
        
    }

    fillObjectProperties() {
  
        for ( let property in objDesc[ this.getObjectTypeByName( this.props.data.name ) ].actions[ this.props.data.type ] ) {
  
            if ( property !== 'settings' ) {
    
                if ( this.props.data[ property ] &&
                    this.checkCompatibility( this.props.data[ property ],
                    objDesc[ this.getObjectTypeByName( this.props.data.name ) ].actions[ this.props.data.type ][ property ][ 1 ] ) ) {
        
                    this.props.data[ property ] = this.props.data[ property ];
    
                } else {
        
                    this.props.data[ property ] =
                        objDesc[ this.getObjectTypeByName( this.props.data.name ) ].actions[ this.props.data.type ][ property ][ 2 ];
        
                }
    
            }
  
        }
  
        // console.log( "------------" );
        // console.log( this.props.data.name );
        // console.log( this.props.data.type );
        // console.log( objDesc[ this.getObjectTypeByName( this.props.data.name ) ] );
        // console.log( objDesc[ this.getObjectTypeByName( this.props.data.name ) ].actions[ this.props.data.type ] );
        // console.log( objDesc[ this.getObjectTypeByName( this.props.data.name ) ].actions[ this.props.data.type ].settings );
        // console.log( "------------" );
        let settings = objDesc[ this.getObjectTypeByName( this.props.data.name ) ].actions[ this.props.data.type ].settings;
      
        for ( let setting in settings ) {
    
            this.props.data.settings ??= {};
            
            if ( this.props.data.settings[ setting ] &&
                this.checkCompatibility( this.props.data.settings[ setting ],
                settings[ setting ][ 1 ] ) ) {
    
                this.props.data.settings[ setting ] = this.props.data.settings[ setting ];
    
            } else {
    
                this.props.data.settings[ setting ] = settings[ setting ][ 2 ];
    
            }
    
        }
  
    }
  
  /**
   * Check if the type of the value is compatible with the type in parameter
   * @param {*} ValueToCheck 
   * @param {*} type 
   */
    checkCompatibility( valueToCheck, type ) {
  
      let typeToCheck = typeof valueToCheck;
  
      switch ( type ) {
  
        case 'String':
        case 'Color':
        case 'Select':
          return typeToCheck === "string";
  
        case 'Number':
          return typeToCheck === "number";
  
        case 'Boolean':
          return typeToCheck === "boolean";
  
        case 'Position':
          return typeToCheck === "object";
  
        case 'Array':
          return Array.isArray( valueToCheck );
            
        default:
          return false;
  
      }
  
    }

    handleClick = () => {

        let mode = this.state.mode === 'overview' ? 'modification' : 'overview';
        this.setState( { ...this.state, mode: mode } );

    }

    handleDelete = (event) => {

        event.stopPropagation();
        this.props.deleteData( this.props.id );

    }

    truncate(str,n) {
        return str ? (str.length > n) ? str.slice(0, n) + '...' : str : "";
    };


    cardModificationContent( cardDesc ) {

        let content = [];
        content.push( this.getNameInput() );
        content.push( this.getTimerInput() );

        if ( !!cardDesc ) {

            content.push( this.getTypeInput() );

            for ( let property in cardDesc ) {

                if ( property !== 'settings' ) {

                    content.push( this.getInput( cardDesc[ property ], [ property ] ) );

                }

            }
            
            for ( let setting in cardDesc.settings ) {

                content.push( this.getInput( cardDesc.settings[ setting ], [ 'settings', setting ] ) );

            }

        }

        return content;

    }
  
    // getNameInput() {

    //     return Inputs.SelectInput( "Objet", [ 'name' ], this.props, { type: "Object" } );

    // }

    handleChangeName( event ) {
        
        this.props.data.name = event.target.value;

        this.checkType();
        this.fillObjectProperties();

        this.props.updateData( this.props.id, this.props.data );

    }
    
    getNameInput() {

        let listOption = [];
        
        for ( let object of this.props.objectNameList ) {

            listOption.push(
                <option
                key={ Math.floor( Math.random() * 10000 ) }
                title={ object.name }
                value={ object.name }>
                { object.name }
                </option> 
            );

        }

        return (
        <label className="input_select" htmlFor="objectName" >
            <span>Objet</span>
            <select
                name="objectName"
                value={ this.props.data.name }
                onChange={ (event) => this.handleChangeName( event ) }>
                { listOption }
            </select>
        </label>
        );

    }
    
    getTimerInput() {

        return Inputs.NumberInput( "Timestep", [ 'timestep' ], this.props, { "min": 0 } );

    }

    handleChangeType( event ) {
        
        this.props.data.type = event.target.value;

        this.fillObjectProperties();

        this.props.updateData( this.props.id, this.props.data );

    }

    getObjectTypeByName( objName ) {

        for ( let object of this.props.objectNameList ) {

            if ( object.name === objName ) {

                return object.information.type;

            }

        }

    }

    getTypeInput() {

        let listOption = [];
        
        for ( let action in objDesc[ this.getObjectTypeByName( this.props.data.name ) ].actions ) {

            listOption.push(
                <option
                key={ Math.floor( Math.random() * 10000 ) }
                title={ action }
                value={ action }>
                { action }
                </option> 
            );

        }

        return (
            <label className="input_select" htmlFor="type" >
                <span>Type</span>
                <select
                    name="type"
                    value={ this.props.data.type }
                    onChange={ (event) => this.handleChangeType( event ) }>
                    { listOption }
                </select>
            </label>
        );

    }

    getInput( inputParam, depth ) {

        switch ( inputParam[ 1 ] ) {

            case 'String':
                return Inputs.StringInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

            case 'Number':
                return Inputs.NumberInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

            case 'Boolean':
                return Inputs.BooleanInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

            case 'Color':
                return Inputs.ColorInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

            case 'Select':
                return Inputs.SelectInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

            case 'Position':
                return Inputs.PositionInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

            case 'Array':
                return Inputs.ArrayInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );
                
            default:
                break;

        }

    }

    typeExist() {

        for ( const type in objDesc[ this.getObjectTypeByName( this.props.data.name ) ].actions ) {

            if ( type === this.props.data.type ) return true;

        }

        return false;
    }

    checkType() {

        console.log( this.typeExist() );
        if ( !this.typeExist() ) {

            this.props.data.type = Object.keys( objDesc[ this.getObjectTypeByName( this.props.data.name ) ].actions )[ 0 ];
            
        }

    }


    render() {

        let content;

        // this.checkType();
        // this.fillObjectProperties();
        
        if ( this.state.mode === 'overview' ) {
            
            content = <div className='card_content'>
                <div className='card_content_left'>
                <span id='name'>{ this.truncate( this.props.data.name, 12 ) }</span>
                <span id='type'>{ this.truncate( this.props.data.type, 12 ) }</span>
                <span id='time'>{ this.truncate( String( this.props.data.timestep ), 12 ) }</span>
                </div>
                <div className='card_content_right'>
                <button className='icon_button' id='delete' onClick={this.handleDelete}></button>
                </div>
            </div>

        } else {

            content = <div className='card_content'>
                <div className='card_content_left'>
                { this.cardModificationContent( objDesc[ this.getObjectTypeByName( this.props.data.name ) ]?.actions[ this.props.data.type ] ) }
                </div>
                <div className='card_content_right'>
                <button className='icon_button' id='delete' onClick={this.handleDelete}></button>
                <button className='icon_button' id='collapse' onClick={this.handleClick}></button>
                </div>
            </div>

        }

        return (
            <div className={'card card_action ' + ( this.state.mode ) }
                onClick={ this.state.mode === 'overview' ? this.handleClick : undefined }>
                {content}
            </div>
        )

    }

}


export default ActionCard;