import React from 'react';
import * as Inputs from '../cardInputs.js';
import objDesc from '../objectsDescription.json';

class ObjectCard extends React.Component {

  constructor( props ) {

    super( props );

    this.state = {
      mode: 'overview'
    }

    this.fillObjectProperties();
    this.props.updateData( this.props.id, this.props.data );
    
  }

  fillObjectProperties() {

    for ( let property in objDesc[ this.props.data.information.type ].information ) {

      if ( property !== 'settings' ) {

        if ( this.props.data.information[ property ] !== null &&
          this.checkCompatibility( this.props.data.information[ property ],
          objDesc[ this.props.data.information.type ].information[ property ][ 1 ] ) ) {

            this.props.data.information[ property ] = this.props.data.information[ property ];

        } else {

          this.props.data.information[ property ] = objDesc[ this.props.data.information.type ].information[ property ][ 2 ];

        }

      }

    }

    for ( let setting in objDesc[ this.props.data.information.type ].information.settings ) {

      this.props.data.information.settings ??= {};
      
      if ( this.props.data.information.settings[ setting ] !== null &&
        this.checkCompatibility( this.props.data.information.settings[ setting ],
          objDesc[ this.props.data.information.type ].information.settings[ setting ][ 1 ] ) ) {

          this.props.data.information.settings[ setting ] = this.props.data.information.settings[ setting ];

      } else {

        this.props.data.information.settings[ setting ] = objDesc[ this.props.data.information.type ].information.settings[ setting ][ 2 ];

      }

    }

  }

/**
 * Check if the type of the value is compatible with the type in parameter
 * @param {*} ValueToCheck 
 * @param {*} type 
 */
  checkCompatibility( valueToCheck, type ) {

    let typeToCheck = typeof valueToCheck;

    switch ( type ) {

      case 'String':
      case 'Color':
      case 'Select':
        return typeToCheck === "string";

      case 'Number':
        return typeToCheck === "number";

      case 'Boolean':
        return typeToCheck === "boolean";

      case 'Position':
        return typeToCheck === "object";

      case 'Array':
        return Array.isArray( valueToCheck );
          
      default:
        return false;

    }

  }

  handleClick = () => {

    let mode = this.state.mode === 'overview' ? 'modification' : 'overview';
    this.setState( { ...this.state, mode: mode } );

  }

  handleDelete = (event) => {

    event.stopPropagation();
    this.props.deleteData( this.props.id );

  }

  truncate(str,n) {
    return (str.length > n) ? str.slice(0, n) + '...' : str;
  };


  cardModificationContent( cardDesc ) {

    let content = [];
    content.push( this.getNameInput() );
    content.push( this.getTypeInput() );

    for ( let property in cardDesc.information ) {

      if ( property !== 'settings' ) {

        content.push( this.getInput( cardDesc.information[ property ], [ 'information', property ] ) );

      }

    }
    
    for ( let setting in cardDesc.information.settings ) {

      content.push( this.getInput( cardDesc.information.settings[ setting ], [ 'information', 'settings', setting ] ) );

    }

    return content;

  }
  
  getNameInput() {

    return Inputs.StringInput( "Nom", [ 'name' ], this.props, {} );

  }

  handleChangeType( event ) {
    
    this.props.data.information.type = event.target.value;

    this.fillObjectProperties();

    this.props.updateData( this.props.id, this.props.data );

  }

  getTypeInput() {

    let listOption = [];
    
    for ( let object in objDesc ) {

      listOption.push(
        <option
          key={ Math.floor( Math.random() * 10000 ) }
          title={ object }
          value={ object }>
          { object }
        </option> 
      );

    }

    return (
      <label className="input_select" htmlFor="type" >
          <span>Type</span>
          <select
              name="type"
              value={ this.props.data.information.type }
              onChange={ (event) => this.handleChangeType( event ) }>
              { listOption }
          </select>
      </label>
    );

  }

  getInput( inputParam, depth ) {

    switch ( inputParam[ 1 ] ) {

        case 'String':
            return Inputs.StringInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

        case 'Number':
            return Inputs.NumberInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

        case 'Boolean':
            return Inputs.BooleanInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

        case 'Color':
            return Inputs.ColorInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

        case 'Select':
            return Inputs.SelectInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

        case 'Position':
            return Inputs.PositionInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );

        case 'Array':
            return Inputs.ArrayInput( inputParam[ 0 ], depth, this.props, inputParam[ 3 ] ?? {} );
            
        default:
            break;

    }

}


  render() {

    let content;

    if ( this.state.mode === 'overview' ) {
        
      content = <div className='card_content'>
        <div className='card_content_left'>
          <span id='name'>{ this.truncate( this.props.data.name, 12 ) }</span>
          <span id='type'>{ this.truncate( this.props.data.information.type, 12 ) }</span>
        </div>
        <div className='card_content_right'>
          <button className='icon_button' id='delete' onClick={this.handleDelete}></button>
        </div>
      </div>

    } else {

      content = <div className='card_content'>
        <div className='card_content_left'>
          { this.cardModificationContent( objDesc[ this.props.data.information.type ] ) }
        </div>
        <div className='card_content_right'>
          <button className='icon_button' id='delete' onClick={this.handleDelete}></button>
          <button className='icon_button' id='collapse' onClick={this.handleClick}></button>
        </div>
      </div>

    }

    return (
      <div className={'card card_object ' + ( this.state.mode ) }
        onClick={ this.state.mode === 'overview' ? this.handleClick : undefined }>
        {content}
      </div>
    )

  }

}


export default ObjectCard;