import React from 'react';
import LinesImg from "../../../assets/images/templates/lines.png";
import PFlowImg from "../../../assets/images/templates/pointFlow.png";
import HexaImg from "../../../assets/images/templates/hmHexa.png";

class DataObjectSectionT extends React.Component {

    selectObject( event, obj ) {

        this.props.updateNeed( this.props.needDetails.name, obj );

    }

    render() {
    
        return (
            <div className={'section_body' } id='DataObjectT_section'>
                <div className='card_group'>
                    <div className={ 'card card_DataObjectT' + ( this.props.listNeed === "Lines" ? ' active' : '' ) }
                    style={ { backgroundImage: 'url(' + LinesImg + ')' } }
                    onClick={ ( event ) => this.selectObject( event, 'Lines' ) }>
                        <div className='card_content'>
                            <span>Lignes</span>
                        </div>
                    </div>
                    <div className={ 'card card_DataObjectT' + ( this.props.listNeed === "PointFlow" ? ' active' : '' ) }
                    style={ { backgroundImage: 'url(' + PFlowImg + ')' } }
                    onClick={ ( event ) => this.selectObject( event, 'PointFlow' ) }>
                        <div className='card_content'>
                            <span>Particules</span>
                        </div>
                    </div>
                    <div className={ 'card card_DataObjectT' + ( this.props.listNeed === "HMHexa" ? ' active' : '' ) }
                    style={ { backgroundImage: 'url(' + HexaImg + ')' } }
                    onClick={ ( event ) => this.selectObject( event, 'HMHexa' ) }>
                        <div className='card_content'>
                            <span>Hexagones</span>
                        </div>
                    </div>
                </div>
            </div>
        )

    }

}


export default DataObjectSectionT;