import React from 'react';
import './JsonGenerator.scss';
import JSONEditorComp from './jsonEditor.js';
import JsonIHM from './jsonIHM/jsonIHM.js';

class JsonGenerator extends React.Component {

  constructor( props ) {

    super( props );

    this.state = {
      displayMode: 'ihm'
    }

  }

  handleClick = () => {
    if ( this.state.displayMode === 'txt' ) {

      this.setState( { ...this.state, displayMode: "ihm"  } );

    } else {

      this.setState( { ...this.state, displayMode: "txt"  } );

    }
  }

  onClickDownload() {
    
    // create file in browser
    const json = JSON.stringify( this.props.data.jsonData, null, 2 );
    const blob = new Blob([json], { type: "application/json" });
    const href = URL.createObjectURL(blob);

    // create "a" HTLM element with href to file
    const link = document.createElement("a");
    link.href = href;
    link.download = "scene.json";
    document.body.appendChild(link);
    link.click();

  }

  updateJSON = (json) => {
    this.props.onChange( json );
  };
  
  onClickRedirect() {

    const json = JSON.stringify( this.props.data.jsonData, null, 2 );

    navigator.clipboard.writeText( json ).then( function() {

      window.location.replace( "https://renderline.github.io/?clipboard=true" );

    } );


  }

  render() {

    let content;
    if ( this.state.displayMode === 'txt' ) {

      content = <JSONEditorComp
        json={this.props.data.jsonData}
        key={this.props.data.keyJson}
        updateJSON={this.updateJSON}
        />;

    } else {

      content = <JsonIHM
        json={this.props.data.jsonData}
        key={this.props.data.keyJson}
        updateJSON={this.updateJSON}
        />;

    }

    return (
      <div className='json_generator'>
        
        <div className='json_head'>
          <span className='title'>Configuration</span>
          <button  className={this.state.displayMode === "txt" ? "switch right" : "switch left"} onClick={this.handleClick}>
            <span className='left'>Cartes</span>
            <span className='right'>Json</span>
          </button>
        </div>
        {content}
        <div className='buttons_template'>
          <button id="redirectRenderline_Template" className='text_button'
          onClick={ ( event ) => this.onClickRedirect( event ) }
          title="Copie le fichier de configuration et redirige vers le site du moteur"
          href=''>
            Lancer le moteur
          </button>
          <button id="downloadJson_Template" className='text_button'
          onClick={ ( event ) => this.onClickDownload( event ) }
          href=''
          >Tlcharger</button>
        </div> 
      </div>
    )

  }

}


export default JsonGenerator;