import React from 'react';
import VideoSection from './video/videoSection';
import DataSection from './data/dataSection';
import DataProcessSection from './dataProcessing/dataProcessSection';
import ObjectSection from './objectSection/objectSection';
import ActionSection from './action/actionSection';
import CameraMoveSection from './cameraMove/cameraMoveSection';
import './jsonIHM.scss';

class JsonIHM extends React.Component {

  constructor( props ) {

    super( props );

    this.state= {
        isCollapsed: {
            video: false,
            data: false,
            dataProcess: false,
            object: false,
            action: false,
            cameraMove: false
        },
        videoOrImage: this.props.json.video ? 'video' : 'image'
    }

    this.savedVideoParameter = this.props.json.video ? 
        {
            settings : {
                cameraPosition: { lat: 46, long: 1, alt: 0 },
                lookAt: { lat: 46, long: 1, alt: 0 },
                width: 1920,
                height: 1080
            }
        } :
        {
            settings : {
                fps: 60,
                duration: 3000,
                width: 1920,
                height: 1080
            },
            cameraMoves: []
        };

  }
  
  expandCollapse = ( section ) => {
      
    let isCollapsed = { ...this.state.isCollapsed };

    switch (section ) {

        case 'video' :
            isCollapsed.video = !isCollapsed.video;
            break;

        case 'data' :
            isCollapsed.data = !isCollapsed.data;
            break;

        case 'dataProcess' :
            isCollapsed.dataProcess = !isCollapsed.dataProcess;
            break;

        case 'object' :
            isCollapsed.object = !isCollapsed.object;
            break;

        case 'action' :
            isCollapsed.action = !isCollapsed.action;
            break;

        case 'cameraMove' :
            isCollapsed.cameraMove = !isCollapsed.cameraMove;
            break;

        default:
            break;

    }

    this.setState( { ...this.state, isCollapsed: isCollapsed } );

  }

  handleClickVideo = () => {

        if ( this.state.videoOrImage === 'video' ) {

            this.props.json.image = structuredClone( this.savedVideoParameter );
            this.savedVideoParameter = structuredClone( this.props.json.video );
            delete this.props.json.video;
            this.setState( { ...this.state, videoOrImage: "image"  } );

        } else {

            this.props.json.video = structuredClone( this.savedVideoParameter );
            this.savedVideoParameter = structuredClone( this.props.json.image );
            delete this.props.json.image;
            this.setState( { ...this.state, videoOrImage: "video"  } );

        }

  }

  refactorDataName () {

  }

  refactorDataProcessingName () {
      
  }

  refactorObjectName () {
      
  }

  render() {

    return (
      <div className='json_ihm'>
        <div className='section' id="video">
            <div className='section_title'>
                <button className={this.state.videoOrImage === "image" ? "switch right" : "switch left"} onClick={this.handleClickVideo}>
                    <div className='left_name'>
                        <span></span>
                        <span>Video</span>
                    </div>
                    <div className='right_name'>
                        <span></span>
                        <span>Image</span>
                    </div>
                </button>
            </div>
            <VideoSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.video}
                videoOrImage={this.state.videoOrImage}
            />
        </div>
        <div className='section' id="data">
            <div className='section_title' onClick={ () => this.expandCollapse('data')}>
                <span></span>
                <span>Data</span>
            </div>
            <span></span>
            <DataSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.data}
            />
        </div>
        <div className='section' id="data_process">
            <div className='section_title' onClick={ () => this.expandCollapse('dataProcess')}>
                <span></span>
                <span>Data Processing</span>
            </div>
            <DataProcessSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.dataProcess}
            />
        </div>
        <div className='section' id="object">
            <div className='section_title' onClick={ () => this.expandCollapse('object')}>
                <span></span>
                <span>Object</span>
            </div>
            <ObjectSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.object}
            />
        </div>
        <div className='section' id="action">
            <div className='section_title' onClick={ () => this.expandCollapse('action')}>
                <span></span>
                <span>Action</span>
            </div>
            <ActionSection
                json={this.props.json}
                updateJSON={this.props.updateJSON}
                isCollapsed={this.state.isCollapsed.action}
            />
        </div>

        { this.state.videoOrImage === "video" ?
            <div className='section' id="camera_move">
                <div className='section_title' onClick={ () => this.expandCollapse('cameraMove')}>
                    <span></span>
                    <span>Camera Move</span>
                </div>
                <CameraMoveSection
                    json={this.props.json}
                    updateJSON={this.props.updateJSON}
                    isCollapsed={this.state.isCollapsed.cameraMove}
                />
            </div>
            :
            null
        }
      </div>
    )

  }

}


export default JsonIHM;