import { Outlet, Link } from "react-router-dom";
import { useLocation } from "react-router-dom";
import logo from "../../assets/images/logo-RenderLine-without-text-line.svg";
import './Header.scss';

const Header = () => {

  //assigning location variable
  const location = useLocation();

  //destructuring pathname from location
  const { pathname } = location;

  //Javascript split method to get the name of the path in array
  const splitLocation = pathname.split("/");

  return (
    <>
      <div className="header">
        <div className="header_logo">
          <img src={logo} alt="logo Renderline" />
        </div>
        <div className="header_state">
          {/* <Link to="/" className={splitLocation[1] === "" ? "active" : ""}>Home</Link>
          <Link to="/feat" className={splitLocation[1] === "feat" ? "active" : ""}>Feat</Link> */}
          <Link to="/template" className={splitLocation[1] === "template" ? "active" : ""}>Template</Link>
          <Link to="/json" className={splitLocation[1] === "json" ? "active" : ""}>Configuration</Link>
        </div>
      </div>

      <div className="content">
        <Outlet />
      </div>
    </>
  )
};

export default Header;