import React from 'react';
import Select from 'react-select';
import positionsFile from '../../../assets/PNPXMLParsed.json';

function truncate(str,n) {
    return (str.length > n) ? str.slice(0, n) + '...' : str;
};

function getValue( depth, data ) {

    let v = data;
    depth.map( ( d ) => v = v[ d ] );
    return v;

}

function updateObj( props, depth, value ) {

    let newData = updateObjRec( props.data, depth, value );
    props.updateData( props.id, newData );

}

function updateObjRec( obj, depth, value ) {
  
	let newObj;
    if ( depth.length > 1 ) {

        let arg = depth.shift();
        newObj = { ...obj, [ arg ]: updateObjRec( obj[arg], depth, value ) };
        return newObj;

    } else {
		
        newObj = { ...obj, [ depth[ 0 ] ]: value};
        return newObj;

    }

}


export function StringInput( label, depth, props ) {

    return (
        <label className="input_string">
            <span>{ label }</span>
            <input
                value={ getValueString( depth, props.data ) }
                onChange={ (event) => handleChangeString( event, props, depth ) }
            />
        </label>
    );

}

function getValueString( depth, data ) {

    let value = getValue( depth, data );
    return value === null || value === undefined ? "" : value;

}

function handleChangeString( event, props, depth ) {
    
    let value = event.target.value;
    updateObj( props, depth, value );

}



export function NumberInput( label, depth, props, params ) {

    return (
        <label className="input_number">
            <span>{ label }</span>
            <input
                type="number"
                min={ params.min ?? "" }
                max={ params.max ?? "" }
                step={ params.step ?? "any" }
                value={ getValueNumber( depth, props.data ) }
                onChange={ (event) => handleChangeNumber( event, props, depth ) }
            />
        </label>
    );

}

function getValueNumber( depth, data ) {

    let value = getValue( depth, data );
    return value === null || value === undefined ? '' : value;

}

function handleChangeNumber( event, props, depth ) {
    
    let value = event.target.value;

    if ( value === '' || isNaN( value ) ) {

      value = null;

    } else {

      value = parseFloat(value);
      if ( event.target.max ) value = Math.min( value, event.target.max );
      if ( event.target.min ) value = Math.max( value, event.target.min );

    }

    updateObj( props, depth, value );

}


export function BooleanInput( label, depth, props, params ) {

    return (
        <label className="input_boolean" htmlFor={label}>
            <input
                type="checkbox"
                id={label}
                checked={ getValueBoolean( depth, props.data ) }
                onChange={ (event) => handleChangeBoolean( event, props, depth ) }
            />
            <span>{ label }</span>
        </label>
    );

}

function getValueBoolean( depth, data ) {
    console.log( "value" );

    let value = getValue( depth, data );
    console.log( value );
    return value === null || value === undefined ? false : value;

}

function handleChangeBoolean( event, props, depth ) {
    
    let value = event.target.checked;
    console.log( value );
    updateObj( props, depth, value );

}


export function ColorInput( label, depth, props, params ) {

    return (
        <label className="input_color" htmlFor={ label } >
            <input
                type="color"
                id={ label }
                value={ getValueColor( depth, props.data ) }
                onChange={ (event) => handleChangeColor( event, props, depth ) }
                />
            <span>{ label }</span>
        </label>
    );

}

function getValueColor( depth, data ) {

    let value = getValue( depth, data );
    return value === null || value === undefined ? '#ffffff' : value;

}

function handleChangeColor( event, props, depth ) {
    
    let value = event.target.value;
    updateObj( props, depth, value );

}


export function PositionInput( label, depth, props, params ) {

    let value = getValuePosition( depth, props.data, params );
    
    const options = [];
    for ( let item of positionsFile.positions ) {
        options.push( {
            value: { lat: parseFloat( item[ 1 ] ), long: parseFloat( item[ 2 ] ) },
            label: item[ 0 ]
        } );
    }

    let latInput = params.lat ?
        <input
            type="number"
            min="-90"
            max="90"
            step="any"
            data-attribute= "lat"
            value={ value.lat }
            onChange={ (event) => handleChangePosition( event, props, depth, params ) }
        />
        : '';

        
    let longInput = params.long ?
        <input
            type="number"
            min="-180"
            max="180"
            step="any"
            data-attribute="long"
            value={ value.long }
            onChange={ (event) => handleChangePosition( event, props, depth, params ) }
        />
        : '';

    let altInput = params.alt ?
        <input
            type="number"
            min="0"
            step="1"
            data-attribute="alt"
            value={ value.alt }
            onChange={ (event) => handleChangePosition( event, props, depth, params ) }
        />
        : '';

    return (
    <div className='input_position'>
        <span>{ label }</span>
        <Select options={ options } onChange={ (event) => selectPositionChange( event, props, depth, params ) }/>
        {
            params.lat ?
            <label title='Latitude'>
                <span>Lat :</span>
                { latInput }
            </label>
            : ''
        }
        {
            params.long ?
            <label title='Longitude'>
                <span>Long :</span>
                { longInput }
            </label>
            : ''
        }
        {
            params.alt ?
            <label title='Altitude'>
                <span>Alt :</span>
                { altInput }
            </label>
            : ''
        }
    </div>
    );

}

function selectPositionChange( event, props, depth, params ) {
    
    let newPos = getValuePosition( depth, props.data, params );

    console.log( positionsFile );
    console.log( newPos );
    newPos[ "lat" ] = event.value.lat;
    newPos[ "long" ] = event.value.long;
    console.log( newPos );

    updateObj( props, depth, newPos );

}

function getValuePosition( depth, data, params ) {

    let value = getValue( depth, data );

    let newPos = {};
    newPos = params.lat ? { ...newPos, lat: '' } : newPos;
    newPos = params.long ? { ...newPos, long: '' } : newPos;
    newPos = params.alt ? { ...newPos, alt: '' } : newPos;
    
    return value === null || value === undefined ? newPos : value;

}

function handleChangePosition( event, props, depth, params ) {
    
    let value = event.target.value;

    if ( value === '' || isNaN( value ) ) {

      value = null;

    } else {

      value = parseFloat(value);
      if ( event.target.max ) value = Math.min( value, event.target.max );
      if ( event.target.min ) value = Math.max( value, event.target.min );

    }

    let newPos = getValuePosition( depth, props.data, params );

    console.log( newPos );
    console.log( event.target.dataset.attribute );
    newPos[ event.target.dataset.attribute ] = value;
    console.log( newPos );

    updateObj( props, depth, newPos );

}


export function SelectInput( label, depth, props, params ) {
    
    let listOption = [];

    switch( params.type ) {
        
        case 'Data':

            if ( isIterable( props.dataNameList ) ) {

                listOption.push(
                    <option
                    key={Math.floor( Math.random()*10000 ) }
                    title="vide"
                    value={ null }>
                    
                    </option> 
                );

                for ( let item of props.dataNameList ) {
            
                    listOption.push(
                        <option
                        key={Math.floor( Math.random()*10000 ) }
                        title={ item.name }
                        value={ item.name }>
                        { item.name }
                        </option> 
                    );
            
                }

            }
            break;
            
        case 'DataProcessing':

            if ( isIterable( props.dataProcessNameList ) ) {

                listOption.push(
                    <option
                    key={Math.floor( Math.random()*10000 ) }
                    title="vide"
                    value={ null }>
                    
                    </option> 
                );

                for ( let item of props.dataProcessNameList ) {
            
                    listOption.push(
                        <option
                        key={Math.floor( Math.random()*10000 ) }
                        title={ item.name }
                        value={ item.name }>
                        { item.name }
                        </option> 
                    );
            
                }
            
            }
            break;

        case 'Object':

            if ( isIterable( props.objectNameList ) ) {
                
                listOption.push(
                    <option
                    key={Math.floor( Math.random()*10000 ) }
                    title="vide"
                    value={ null }>
                    
                    </option> 
                );

                for ( let item of props.objectNameList ) {
            
                    listOption.push(
                        <option
                        key={Math.floor( Math.random()*10000 ) }
                        title={ item.name }
                        value={ item.name }>
                        { item.name }
                        </option> 
                    );
            
                }
            
            }
            break;

        case 'String':

            if ( isIterable( params.options ) ) {

                for ( let item of params.options ) {
            
                    listOption.push(
                        <option
                        key={Math.floor( Math.random()*10000 ) }
                        title={ item }
                        value={ item }>
                        { item }
                        </option> 
                    );
            
                }

            }
            break;

        default:
            break;

    }

    return (
        <label className="input_select" htmlFor={ label } >
            <span>{ label }</span>
            <select
                name={ label }
                value={ getValueSelect( depth, props.data ) }
                onChange={ (event) => handleChangeSelect( event, props, depth ) }>
                { listOption }
            </select>
        </label>
    );

}

function getValueSelect( depth, data ) {

    let value = getValue( depth, data );
    return value === null || value === undefined ? '' : value;

}

function handleChangeSelect( event, props, depth ) {
    
    let value = event.target.value;
    updateObj( props, depth, value );

}



export function ArrayInput( label, depth, props, params ) {

    let itemList = getValue( depth, props.data );

    itemList = typeof itemList === 'string' ? [ itemList ] : itemList;

    let arrayList;

    switch( params.type ) {
        
        case 'Data':
        case 'DataProcessing':
        case 'String':

            arrayList = displayAsListSelect( itemList, props, depth, params );
            break;

        case 'Number %':
            
            arrayList = displayAsListNumberPercent( itemList, props, depth, params );
            break;

        case 'Color %':

            arrayList = displayAsListColorPercent( itemList, props, depth, params );
            break;

        default:
            break;

    }

    return( 
        <div className="input_array" id={params.type}>
            <span className="input_array_label">
                { label }
            </span>
            <div className="input_array_list">
                { arrayList }
            </div>
            <button className="input_array_add mini_button" id="add" onClick={ () => addToArray( props, depth, params ) }><span>+</span></button>
        </div>
    );

}

function displayAsListSelect( itemList, props, depth, params ) {

    if ( !( itemList != null && Symbol.iterator in Object( itemList ) ) ) return '';

    let listOption = [];
    
    switch( params.type ) {
        
        case 'Data':
            
            if ( isIterable( props.dataNameList ) ) {

                for ( let item of props.dataNameList ) {
            
                    listOption.push(
                        <option
                        key={Math.floor( Math.random()*10000 ) }
                        title={ item.name }
                        value={ item.name }>
                        { item.name }
                        </option> 
                    );
            
                }

            }
            break;
            
        case 'DataProcessing':

            if ( isIterable( props.dataProcessNameList ) ) {

                for ( let item of props.dataProcessNameList ) {
            
                    listOption.push(
                        <option
                        key={Math.floor( Math.random()*10000 ) }
                        title={ item.name }
                        value={ item.name }>
                        { item.name }
                        </option> 
                    );
            
                }

            }
            break;

        case 'String':

            if ( isIterable( params.options ) ) {

                for ( let item of params.options ) {
            
                    listOption.push(
                        <option
                        key={Math.floor( Math.random()*10000 ) }
                        title={ item }
                        value={ item }>
                        { item }
                        </option> 
                    );
            
                }

            }
            break;

        default:
            break;

    }

    let list = [];
    let i = 0;
    for ( let item of itemList ) {

        console.log( item );
        list.push(
            <div
            key={ i }
            title={ item }>
                <select
                value={ item }
                data-index={ i }
                onChange={ (event) => handleChangeArraySelect( event, props, depth ) }>
                    { listOption }
                </select>
                <button
                className="mini_button"
                data-index={ i }
                id="delete"
                onClick={ ( event ) => handleDeleteArray( event, props, depth ) } >
                    <span>-</span>
                </button>
            </div>
        );
        i++;

    }
    return list;

}

function handleChangeArraySelect( event, props, depth ) {

    let array = getValue( depth, props.data );
    let newArray = [ ...array ];
    newArray[ event.target.dataset.index ] = event.target.value;
    updateObj( props, depth, newArray );

}

function displayAsListNumberPercent( itemList, props, depth, params ) {

    if ( !( itemList != null && Symbol.iterator in Object( itemList ) ) ) return '';

    let list = [];
    let i = 0;
    for ( let item of itemList ) {

        list.push(
            <div
            key={ i }>
                <input
                    id="percent"
                    type="number"
                    min="-100"
                    max="100"
                    step="any"
                    data-index={ i }
                    value={ item[ 1 ] }
                    onChange={ (event) => handleChangeArrayNumberPercent( event, props, depth ) }
                />
                <span>%</span>
                <input
                    id="value"
                    type="number"
                    min={ params.min ?? "" }
                    max={ params.max ?? "" }
                    step={ params.step ?? "any" }
                    data-index={ i }
                    value={ item[ 0 ] }
                    onChange={ (event) => handleChangeArrayNumberPercentValue( event, props, depth ) }
                />
                <button
                className="mini_button"
                data-index={ i }
                id="delete"
                onClick={ ( event ) => handleDeleteArray( event, props, depth ) } >
                    <span>-</span>
                </button>
            </div>
        );
        i++;

    }
    return list;

}

function handleChangeArrayNumberPercent( event, props, depth ) {

    let value = event.target.value;

    if ( value === '' || isNaN( value ) ) {

        value = null;
  
    } else {
  
        value = parseFloat(value);
        if ( event.target.max ) value = Math.min( value, event.target.max );
        if ( event.target.min ) value = Math.max( value, event.target.min );
  
    }

    let array = getValue( depth, props.data );
    array[ event.target.dataset.index ][ 1 ] = value;
    updateObj( props, depth, array );

}

function handleChangeArrayNumberPercentValue( event, props, depth ) {

    let value = event.target.value;

    if ( value === '' || isNaN( value ) ) {

        value = null;
  
    } else {
  
        value = parseFloat(value);
        if ( event.target.max ) value = Math.min( value, event.target.max );
        if ( event.target.min ) value = Math.max( value, event.target.min );
  
    }

    let array = getValue( depth, props.data );
    array[ event.target.dataset.index ][ 0 ] = value;
    updateObj( props, depth, array );

}

function displayAsListColorPercent( itemList, props, depth, params ) {

    if ( !( itemList != null && Symbol.iterator in Object( itemList ) ) ) return '';

    let list = [];
    let i = 0;
    for ( let item of itemList ) {

        list.push(
            <div
            key={ i }>
                <input
                    id="percent"
                    type="number"
                    min="-100"
                    max="100"
                    step="any"
                    data-index={ i }
                    value={ item[ 1 ] }
                    onChange={ (event) => handleChangeArrayColorPercent( event, props, depth ) }
                />
                <span>%</span>
                <input
                    id="value"
                    type="color"
                    data-index={ i }
                    value={ item[ 0 ] }
                    onChange={ (event) => handleChangeArrayColorPercentValue( event, props, depth ) }
                />
                <button
                className="mini_button"
                data-index={ i }
                id="delete"
                onClick={ ( event ) => handleDeleteArray( event, props, depth ) } >
                    <span>-</span>
                </button>
            </div>
        );
        i++;

    }
    return list;

}

function handleChangeArrayColorPercent( event, props, depth ) {

    let value = event.target.value;

    if ( value === '' || isNaN( value ) ) {

        value = null;
  
    } else {
  
        value = parseFloat(value);
        if ( event.target.max ) value = Math.min( value, event.target.max );
        if ( event.target.min ) value = Math.max( value, event.target.min );
  
    }

    let array = getValue( depth, props.data );
    array[ event.target.dataset.index ][ 1 ] = value;
    updateObj( props, depth, array );

}

function handleChangeArrayColorPercentValue( event, props, depth ) {

    let array = getValue( depth, props.data );
    array[ event.target.dataset.index ][ 0 ] = event.target.value;
    updateObj( props, depth, array );

}

function handleDeleteArray( event, props, depth ) {
    
    let array = getValue( depth, props.data );
    array.splice( event.target.dataset.index, 1 );
    updateObj( props, depth, array );

}

function addToArray( props, depth, params ) {

    let newItem;

    switch( params.type ) {
        
        case 'Data':
            
            newItem = props.dataNameList ? props.dataNameList[ 0 ] ?? '' : '';
            break;

        case 'DataProcessing':
            
            newItem = props.dataProcessNameList ? props.dataProcessNameList[ 0 ] ?? '' : '';
            break;

        case 'String':

            newItem = params.options ? params.options[ 0 ] ?? '' : '';
            break;

        case 'Number %':

            newItem = [ 0, 0 ];
            break;

        case 'Color %':

            newItem = [ '#ffffff', 0 ];
            break;

        default:
            break;

    }
    
    let array = getValue( depth, props.data );
    array.push( newItem );
    updateObj( props, depth, array );

}


/**
 * Determine whether the given `input` is iterable.
 *
 * @returns {Boolean}
 */
function isIterable(input) {  
    if (input === null || input === undefined) {
        return false
    }
  
    return typeof input[Symbol.iterator] === 'function'
}
