import './TemplateSelector.scss';
import React from 'react';
import templateList from "./templates/templateList.js";
import DataSectionT from "./Sections/dataSectionT.js";
import AreaSectionT from "./Sections/areaSection.js";
import SettingsSectionT from "./Sections/settingsSectionT.js";
import DataObjectSectionT from "./Sections/dataObjectSectionT.js";
import { Link } from "react-router-dom";
// import GlobalTemplate from "../../templateGenerator/template/globalTemplate.js";

class TemplateSelector extends React.Component {

  constructor( props ) {

    super( props );

    this.state = {
      displayNeeds: false,
      indexTemplateSelected: -1,
      needs: {}
    }

  }

  // addNeeds( needSection, needIndex, newNeed ) {

  //   this.state.needs[ needSection ][ needIndex ].push( newNeed );
  //   this.setState( ...this.state, )

  // }
  
  // deleteNeeds( needSection, needIndex, newNeed ) {

  //   this.state.needs[ needSection ][ needIndex ] = newNeed;
  //   this.setState( ...this.state, )

  // }

  updateNeeds = ( needSection, newNeed ) => {

    this.state.needs[ needSection ] = newNeed;
    this.setState( { ...this.state, needs: this.state.needs } );

  }

  updateJSON = (json) => {
    this.props.onChange( json );
  };

  handleClick( event ) {

    // let newJson = templateList[ event.target.dataset.index ].json;
    // this.updateJSON( newJson );
    let newIndexTemplateSelected = parseInt( event.target.dataset.index );
    let newDisplayNeeds = !!templateList[ newIndexTemplateSelected ].needs;
    let newNeeds = {};
    
    for ( const need of templateList[ newIndexTemplateSelected ].needs ) {
      newNeeds[ need.name ] = [];
    }

    this.setState( {
      ...this.state,
      indexTemplateSelected: newIndexTemplateSelected,
      displayNeeds: newDisplayNeeds,
      needs: newNeeds
    } );
    
  }

  onClickDownload() {

    let jsonToDl = this.generateTemplate();

    // create file in browser
    const json = JSON.stringify( jsonToDl, null, 2 );
    const blob = new Blob([json], { type: "application/json" });
    const href = URL.createObjectURL(blob);

    // create "a" HTLM element with href to file
    const link = document.createElement("a");
    link.href = href;
    link.download = "scene.json";
    document.body.appendChild(link);
    link.click();

  }

  onClickRedirect() {

    let jsonToUse = this.generateTemplate();
    const json = JSON.stringify( jsonToUse, null, 2 );

    navigator.clipboard.writeText(json).then(function() {
      window.location.replace("https://renderline.github.io/?clipboard=true");
    });


  }


  generateTemplate() {
    console.log( templateList[ this.state.indexTemplateSelected ].generator.json );
    let templateGen = new Function(
      "needs",
      "json",
      templateList[ this.state.indexTemplateSelected ].generator.function.join("")
    );
    let newJson = templateGen( this.state.needs, templateList[ this.state.indexTemplateSelected ].generator.json );
    this.updateJSON( newJson );
    return newJson;

  }

  needsContent() {

    if ( !this.state.displayNeeds ) {

      return "";

    } else {

      let template_needs = [];

      for ( const need of templateList[ this.state.indexTemplateSelected ].needs ) {

        switch ( need.type ) {

          case 'Data' : {

            template_needs.push(
              <div className='section' id="data">
                <div className='section_title'>
                  <span>{ need.label }</span>
                </div>
                <DataSectionT
                  needDetails={ need }
                  listNeed={ this.state.needs[ need.name ] }
                  updateNeed={ this.updateNeeds }
                />
              </div>
            );
            break;

          }
          case 'DataObject' : {

            template_needs.push(
              <div className='section' id="data">
                <div className='section_title'>
                  <span>{ need.label }</span>
                </div>
                <DataObjectSectionT
                  needDetails={ need }
                  listNeed={ this.state.needs[ need.name ] }
                  updateNeed={ this.updateNeeds }
                />
              </div>
            );
            break;

          }
          
          case 'Area' : {

            template_needs.push(
              <div className='section' id="data">
                <div className='section_title'>
                  <span>{ need.label }</span>
                </div>
                <AreaSectionT
                  needDetails={ need }
                  listNeed={ this.state.needs[ need.name ] }
                  updateNeed={ this.updateNeeds }
                />
              </div>
            );
            // <AreaSectionT
            //   listNeed={ this.state.needs[ need.name ] }
            //   updateNeed={ this.updateNeeds }
            // />
            break;

          }

          case 'Settings' : {

            template_needs.push(
              <div className='section' id="data">
                <div className='section_title'>
                  <span>{ need.label }</span>
                </div>
                <SettingsSectionT
                  needDetails={ need }
                  listNeed={ this.state.needs[ need.name ] }
                  updateNeed={ this.updateNeeds }
                />
              </div>
            );
            break;

          }


          default: {break;}

        }

      }
      
      return (
      <div className="template_needs">
        { template_needs }
      </div>
      );
    }

  }

  render() {

    let cardList = [];
    let index = 0;

    for ( const template of templateList ) {

      cardList.push(
        <div
          className={ "card card_template " + ( this.state.indexTemplateSelected === index? 'selected' : '' ) }
          style={ { backgroundImage: 'url('+template.image+')' } }
          data-index={ index }
          onClick={ ( event ) => this.handleClick( event ) }
        >
          <div className='info_container'>
            <span className='name'>{ template.name }</span>
            <span className='separateur'></span>
            <span className='desc'>{ template.description }</span>
          </div>
        </div>
      );
      index++;

    }

    return (
      <div className='template'>
        <span className='title'>Choisir un template</span>
        <div className='template_container'>
          { cardList }
        </div>
        { this.needsContent() }
        <div className={ this.state.indexTemplateSelected === -1 ? 'buttons_template hide' : 'buttons_template' }>
          <button id="redirectRenderline_Template" className='text_button'
          onClick={ ( event ) => this.onClickRedirect( event ) }
          title="Copie le fichier de configuration et redirige vers le site du moteur"
          href=''>
            Lancer le moteur
          </button>
          <button id="downloadJson_Template" className='text_button'
          onClick={ ( event ) => this.onClickDownload( event ) }
          href=''>
            Tlcharger
          </button>
          <Link to="/json" className="text_button"
          onClick={ () => this.generateTemplate() }>
            <span>Configuration</span>
          </Link>
        </div>

      </div>
    )

  }

}

  
export default TemplateSelector;