import React from 'react';
import worldImg from "../../../assets/images/templates/WorldESA.jpg";
import EuropeImg from "../../../assets/images/templates/EuropeESA.jpg";
import FranceImg from "../../../assets/images/templates/FranceESA.jpg";
import CDGImg from "../../../assets/images/templates/CDGParisAirport.jpg";

class AreaSectionT extends React.Component {

    selectArea( event, area ) {

        this.props.updateNeed( this.props.needDetails.name, area );

    }

    render() {
    
        return (
            <div className={'section_body' } id='areaT_section'>
                <div className='card_group'>
                    <div className={ 'card card_AreaT' + ( this.props.listNeed === "World" ? ' active' : '' ) }
                    style={ { backgroundImage: 'url(' + worldImg + ')' } }
                    onClick={ ( event ) => this.selectArea( event, 'World' ) }>
                        <div className='card_content'>
                            <span>Monde</span>
                        </div>
                    </div>
                    <div className={ 'card card_AreaT' + ( this.props.listNeed === "Europe" ? ' active' : '' ) }
                    style={ { backgroundImage: 'url(' + EuropeImg + ')' } }
                    onClick={ ( event ) => this.selectArea( event, 'Europe' ) }>
                        <div className='card_content'>
                            <span>Europe</span>
                        </div>
                    </div>
                    <div className={ 'card card_AreaT' + ( this.props.listNeed === "France" ? ' active' : '' ) }
                    style={ { backgroundImage: 'url(' + FranceImg + ')' } }
                    onClick={ ( event ) => this.selectArea( event, 'France' ) }>
                        <div className='card_content'>
                            <span>France</span>
                        </div>
                    </div>
                    <div className={ 'card card_AreaT' + ( this.props.listNeed === "CDG" ? ' active' : '' ) }
                    style={ { backgroundImage: 'url(' + CDGImg + ')' } }
                    onClick={ ( event ) => this.selectArea( event, 'CDG' ) }>
                        <div className='card_content'>
                            <span>CDG</span>
                        </div>
                    </div>
                </div>
            </div>
        )

    }

}


export default AreaSectionT;