import React from 'react';
import './dataSection.scss';
import DataCard from './dataCard.js';

class DataSection extends React.Component {

    constructor( props ) {

        super( props );

        let dataList = [];

        if ( this.props.json.scene.data ) {

        dataList = structuredClone( this.props.json.scene.data );
        dataList.map( ( data ) => {
            data.id = Math.floor( Math.random() * Date.now() )
        } );

        }

        this.state = {
            dataList: dataList
        }

    }

    getObjectById( id ) {

        for ( let i = 0; i < this.state.dataList.length; i++ ) {

            if ( this.state.dataList[ i ].id === id ) return { index: i, object: this.state.dataList[ i ] }

        }

    }

    updateData = ( id, newData ) => {

        this.state.dataList[ this.getObjectById( id ).index ] = newData;
        this.setState( { ...this.state, dataList: this.state.dataList } );
        let updatedDataList = structuredClone(  this.state.dataList );
        updatedDataList.map( (data ) => { delete data.id } );
        this.props.json.scene.data = updatedDataList;
        this.props.updateJSON( this.props.json );

    }

    deleteData = ( id ) => {
        
        this.state.dataList.splice( this.getObjectById( id ).index, 1 );
        this.setState( { ...this.state, dataList: this.state.dataList } );
        let updatedDataList = structuredClone(  this.state.dataList );
        updatedDataList.map( (data ) => { delete data.id } );
        this.props.json.scene.data = updatedDataList;
        this.props.updateJSON( this.props.json );

    }

    addData = () => {
        
        this.props.json.scene.data = this.props.json.scene.data ?? [];

        let newData = {
        name: "Data " + Math.floor( Math.random() * 9999 ),
        information: {
            type: "CSV",
            fileName: ""
        }
        };
        this.props.json.scene.data.push( newData );
        this.props.updateJSON( this.props.json );
        this.state.dataList.push( { ...newData, id: Math.floor( Math.random() * Date.now() ) } );
        this.setState( { ...this.state, dataList: this.state.dataList } );

    }

    render() {

        const cardList = [];

        for ( let data of this.state.dataList ) {

            cardList.push(
                <DataCard
                    data={data}
                    dataNameList={ this.props.json.scene.data }
                    dataProcessNameList={ this.props.json.scene.dataProcessing }
                    updateData={ this.updateData }
                    deleteData={ this.deleteData }
                    id={data.id}
                    key={data.id}
                />
            );
    
        }
    
        return (
            <div className={'section_body ' + ( this.props.isCollapsed ? 'collapse' : 'expand') } id='object_section'>
                <div className='card_group'>
                    {cardList}
                </div>
                <div className='section_footer'>
                    <button className='floating_button' id='add' onClick={ this.addData }>+</button>
                </div>
            </div>
        )

    }

}


export default DataSection;