import React from 'react';

class DataSectionT extends React.Component {

    getObjectById( id ) {
        
        for ( let i = 0; i < this.props.listNeed.length; i++ ) {

            if ( this.props.listNeed[ i ].id === id ) return { index: i, object: this.props.listNeed[ i ] }

        }

    }

    deleteData = ( id ) => {
        
        let listUpdated = this.props.listNeed;
        listUpdated.splice( this.getObjectById( id ).index, 1 );
        this.props.updateNeed( this.props.needDetails.name, listUpdated );

    }

    addData = () => {
        
        let newData = {
            id: Math.floor( Math.random() * Date.now() ),
            type: "CSV",
            fileName: ""
        };
        let listUpdated = this.props.listNeed;
        listUpdated.push( newData );
        this.props.updateNeed( this.props.needDetails.name, listUpdated );

    }

    
    getNameInput( id ) {

        return (
            <label className="input_string">
                <span>Nom du fichier</span>
                <input
                    value={ this.getValueString( id ) }
                    onChange={ (event) => this.handleChangeString( event, id ) }
                />
            </label>
        );

    }

    getValueString( id ) {

        let value = this.getObjectById( id ).object.name;
        return value === null || value === undefined ? "" : value;

    }

    handleChangeString( event, id ) {
        
        let listUpdated = this.props.listNeed;
        listUpdated[ this.getObjectById( id ).index ].name = event.target.value;
        this.props.updateNeed( this.props.needDetails.name, listUpdated );

    }


    getTypeInput( id ) {

        let listOption = [];
        let listType = [ "CSV", "TXT", "Json", "GEO" ];

        for ( let type of listType ) {

            listOption.push(
                <option
                key={ Math.floor( Math.random() * 10000 ) }
                title={ type }
                value={ type }>
                    { type }
                </option> 
            );

        }

        return (
            <label className="input_select" htmlFor="type" >
                <span>Type</span>
                <select
                    name="type"
                    value={ this.props.listNeed[ this.getObjectById( id ).index ].type }
                    onChange={ (event) => this.handleChangeType( event, id ) }>
                    { listOption }
                </select>
            </label>
        );

    }

    handleChangeType( event, id ) {
        
        let listUpdated = this.props.listNeed;
        listUpdated[ this.getObjectById( id ).index ].type = event.target.value;
        this.props.updateNeed( this.props.needDetails.name, listUpdated );

    }

    render() {

        const cardList = [];

        for ( let need of this.props.listNeed ) {

            cardList.push(
                
                <div className={ 'card card_data' }
                key={need.id}>
                    <div className='card_content'>
                        <div className='card_content_left'>
                            { this.getNameInput( need.id ) }
                            { this.getTypeInput( need.id ) }
                        </div>
                        <div className='card_content_right'>
                            <button className='icon_button' id='delete' onClick={this.handleDelete}></button>
                        </div>
                    </div>
                </div>
            );
    
        }
    
        return (
            <div className={'section_body' } id='dataT_section'>
                <div className='card_group'>
                    {cardList}
                </div>
                <div className='section_footer'>
                    <button className='floating_button' id='add' onClick={ this.addData }>+</button>
                </div>
            </div>
        )

    }

}


export default DataSectionT;