import React from 'react';
import './objectSection.scss';
import ObjectCard from './objectCard.js';

class ObjectSection extends React.Component {

  constructor( props ) {

    super( props );

    let objList = [];

    if ( this.props.json.scene.objects ) {

      objList = structuredClone( this.props.json.scene.objects );
      objList.map( ( obj ) => {
        obj.id = Math.floor( Math.random() * Date.now() )
      } );

    }

    this.state = {
      objList: objList
    }

  }

  getObjectById( id ) {

    for ( let i = 0; i < this.state.objList.length; i++ ) {

      if ( this.state.objList[ i ].id === id ) return { index: i, object: this.state.objList[ i ] }

    }

  }

  updateData = ( id, newData ) => {

    this.state.objList[ this.getObjectById( id ).index ] = newData;
    this.setState( { ...this.state, objList: this.state.objList } );
    let updatedObjList = structuredClone( this.state.objList );
    updatedObjList.map( (data ) => { delete data.id } );
    this.props.json.scene.objects = updatedObjList;
    this.props.updateJSON( this.props.json );

  }

  deleteData = ( id ) => {
    
    this.state.objList.splice( this.getObjectById( id ).index, 1 );
    this.setState( { ...this.state, objList: this.state.objList } );
    let updatedObjList = structuredClone( this.state.objList );
    updatedObjList.map( (data ) => { delete data.id } );
    this.props.json.scene.objects = updatedObjList;
    this.props.updateJSON( this.props.json );

  }

  addData = () => {
    
    this.props.json.scene.objects = this.props.json.scene.objects ?? [];

    // console.log( this.props.json.scene.data );

    let newObj = {
      name: "Objet " + Math.floor( Math.random() * 9999 ),
      information: {
        type: "Lines",
        dataName: this.props.json.scene.data[ 0 ]?.name,
        data: {},
        settings: {}
      }
    };
    
    this.props.json.scene.objects.push( newObj );
    this.props.updateJSON( this.props.json );
    this.state.objList.push( { ...newObj, id: Math.floor( Math.random() * Date.now() ) } );
    this.setState( { ...this.state, objList: this.state.objList } );

  }

  render() {

    const cardList = [];

    for ( let data of this.state.objList ) {

      cardList.push(
        <ObjectCard
          data={data}
          dataNameList={ this.props.json.scene.data }
          dataProcessNameList={ this.props.json.scene.dataProcessing }
          updateData={ this.updateData }
          deleteData={ this.deleteData }
          id={data.id}
          key={data.id}
        />
      );

    }

    return (
      <div className={'section_body ' + ( this.props.isCollapsed ? 'collapse' : 'expand') } id='object_section'>
        <div className='card_group'>
          {cardList}
        </div>
        <div className='section_footer'>
          <button className='floating_button' id='add' onClick={ this.addData }>+</button>
        </div>
      </div>
    )

  }

}


export default ObjectSection;