import ReactDOM from "react-dom/client";
import React from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Header from "./pages/Header/Header";
import Home from "./pages/Home/Home";
import FeatExtractor from "./pages/FeatExtractor/FeatExtractor";
import TemplateSelector from "./pages/TemplateSelector/TemplateSelector";
import JsonGenerator from "./pages/JsonGenerator/JsonGenerator";
import './style/main.scss';
import './style/inputs.scss';
import * as DragnDrop from './JsonLoader.js';
import objDesc from './pages/JsonGenerator/jsonIHM/objectsDescription.json';

export default class App extends React.Component  {

  constructor( props ) {

    super(props);

    this.state = {
      featData: undefined,
      jsonData: {

        "title": "Exemple",
    
        "scene" : {
    
            "data": [
                {
                    "name": "Data1",
                    "information": {
                        "fileName": "France060819.csv",
                        "type": "CSV",
                        "settings": {
                        }
                    }
                }
            ],
    
            "objects" : [
                {
                    "name": "Pf1",
                    "information": {
                        "type": "PointFlow",
                        "dataName": "Data1",
                        "settings": {
                            "color": "#2247DB",
                            "opacity": 0.1,
                            "point_size": 5
                        }
                    }
                }
            ],
    
            "actions": [
                {
                    "timestep": 0,
                    "name": "Pf1",
                    "type": "Hide",
                    "settings": {
                    }
                },
    
                {
                    "timestep": 3000,
                    "name": "Pf1",
                    "type": "Show",
                    "settings": {
                        "duration": 2000
                    }
                },
                {
                    "timestep": 11000,
                    "name": "Pf1",
                    "type": "Altitude Factor",
                    "settings": {
                        "factor": 2,
                        "duration": 2000
                    }
                },
                {
                    "timestep": 14000,
                    "name": "Pf1",
                    "type": "Opacity",
                    "settings": {
                        "opacity": 0.03,
                        "duration": 2000
                    }
                },
                {
                    "timestep": 20000,
                    "name": "Pf1",
                    "type": "Opacity",
                    "settings": {
                        "opacity": 0.05,
                        "duration": 2000
                    }
                }
    
            ]
    
        },
        
        "video": {
    
            "settings" : {
                "width": 1920,
                "height": 1080,
                "fps": 60,
                "duration": 30000
            },
    
            "cameraMoves": [
                {
                    "timestep": 0,
                    "type": "Jump",
                    "to": { "lat": 2, "long": -20, "alt": 10000000 },
                    "lookAt": "center"
                },
                {
                    "timestep": 1000,
                    "type": "FromTo",
                    "to": { "lat": 45, "long": 2, "alt": 1000000 },
                    "lookAt": "center",
                    "duration": 2000
                },
                {
                    "timestep": 14000,
                    "type": "FromTo",
                    "to": { "lat": 41, "long": 3, "alt": 60000 },
                    "lookAt": { "lat": 43, "long": 3, "alt": 0 },
                    "duration": 3000,
                    "easeFunction": "InOut",
                    "easePow": 1
                },
                {
                    "timestep": 17000,
                    "type": "FromTo",
                    "to": { "lat": 48.3, "long": 2.6, "alt": 35000 },
                    "lookAt": { "lat": 48.6, "long": 2.4, "alt": 0 },
                    "duration": 4000,
                    "easeFunction": "InOut",
                    "easePow": 1
                },
                {
                    "timestep": 25000,
                    "type": "FromTo",
                    "to": { "lat": 49, "long": 2.4, "alt": 500000 },
                    "lookAt": "center",
                    "duration": 2000
                }
    
            ]
    
        }
    
    },
      keyJson: Math.floor( Math.random() * Date.now() )
    };

    DragnDrop.initDragAndDrop( this );

  }

  
  /**
   * Make sure that the actions linked to the object are possible, otherwise delete the link (action name = null)
   * @param {*} newJson 
   */
   checkAction( newJson ) {

        for ( let action of newJson.scene.actions ) {

            // console.log( action.type );
            // console.log( action.name );

            for ( let object of newJson.scene.objects ) {

                if ( action.name === object.name ) {

                    // console.log( object );

                    if ( !objDesc[ object.information.type ].actions[ action.type ] ) {

                        action.name = "null";

                    }

                }

            }
            // console.log( action.name );

        }

        return newJson;

    }

  updateJson = ( newJson ) => {

    if ( newJson.video ) {

        newJson = this.checkAction( newJson );

    }
    
    this.setState( { ...this.state, jsonData: { ...newJson } } );
    // console.log( this.state.jsonData.scene.data[0] );
    console.log('json updated');

  }

  render() {
    return (
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Header />}>

            {/* <Route index element={<Home />} />
            <Route path="feat" element={<FeatExtractor />} />
            <Route path="template" element={<TemplateSelector data={this.state} onChange={this.updateJson}/>} />
            <Route path="json" element={<JsonGenerator data={this.state} onChange={this.updateJson}/>} />
            <Route path="*" element={<Home />} /> */}

            <Route index element={<TemplateSelector data={this.state} onChange={this.updateJson}/>} />
            {/* <Route path="template" element={<TemplateSelector data={this.state} onChange={this.updateJson}/>} /> */}
            <Route path="json" element={<JsonGenerator data={this.state} onChange={this.updateJson}/>} />
            <Route path="*" element={<TemplateSelector data={this.state} onChange={this.updateJson}/>} />

          </Route>
        </Routes>
      </BrowserRouter>
    );
  }
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(<App />);