import React from 'react';
import * as Inputs from '../../JsonGenerator/jsonIHM/cardInputs.js';

class SettingsSectionT extends React.Component {

    constructor( props ) {

        super( props );

        console.log( props );

        let needSettings = {};

        for ( let setting of this.props.needDetails.settings.data ) {

            needSettings[ setting[ 0 ] ] = setting[ 3 ];

        }

        this.props.updateNeed( this.props.needDetails.name, needSettings );

        console.log( needSettings );
    
    }

    getObjectById( id ) {
        
        for ( let i = 0; i < this.props.listNeed.length; i++ ) {

            if ( this.props.listNeed[ i ].id === id ) return { index: i, object: this.props.listNeed[ i ] }

        }

    }

    deleteData = ( id ) => {
        
        let listUpdated = this.props.listNeed;
        listUpdated.splice( this.getObjectById( id ).index, 1 );
        this.props.updateNeed( this.props.needDetails.name, listUpdated );

    }

    addData = () => {
        
        let newData = {
            id: Math.floor( Math.random() * Date.now() ),
            type: "CSV",
            fileName: ""
        };
        let listUpdated = this.props.listNeed;
        listUpdated.push( newData );
        this.props.updateNeed( this.props.needDetails.name, listUpdated );

    }

    
    getNameInput( id ) {

        return (
            <label className="input_string">
                <span>Nom du fichier</span>
                <input
                    value={ this.getValueString( id ) }
                    onChange={ (event) => this.handleChangeString( event, id ) }
                />
            </label>
        );

    }

    getValueString( id ) {

        let value = this.getObjectById( id ).object.name;
        return value === null || value === undefined ? "" : value;

    }

    handleChangeString( event, id ) {
        
        let listUpdated = this.props.listNeed;
        listUpdated[ this.getObjectById( id ).index ].name = event.target.value;
        this.props.updateNeed( this.props.needDetails.name, listUpdated );

    }


    getTypeInput( id ) {

        let listOption = [];
        let listType = [ "CSV", "TXT", "Json", "GEO" ];

        for ( let type of listType ) {

            listOption.push(
                <option
                key={ Math.floor( Math.random() * 10000 ) }
                title={ type }
                value={ type }>
                    { type }
                </option> 
            );

        }

        return (
            <label className="input_select" htmlFor="type" >
                <span>Type</span>
                <select
                    name="type"
                    value={ this.props.listNeed[ this.getObjectById( id ).index ].type }
                    onChange={ (event) => this.handleChangeType( event, id ) }>
                    { listOption }
                </select>
            </label>
        );

    }

    handleChangeType( event, id ) {
        
        let listUpdated = this.props.listNeed;
        listUpdated[ this.getObjectById( id ).index ].type = event.target.value;
        this.props.updateNeed( this.props.needDetails.name, listUpdated );

    }

    updateSettings = ( id, newData ) => {

        this.props.updateNeed( this.props.needDetails.name, newData );

    }
    
    getInput( setting, objInput ) {
  
        // content.push( Inputs.NumberInput( "Dure en secondes", [ 'duration' ], objTime, { "min": 0, "max": 120 } ) );
        // content.push( Inputs.SelectInput( "Images par secondes", [ 'fps' ], objData, { "type": "String", "options": [ 30, 60 ] } ) );

        objInput.id = setting[ 0 ]
        switch ( setting[ 2 ] ) {

            case 'String':
                return Inputs.StringInput( setting[ 1 ], [ setting[ 0 ] ], objInput, setting[ 4 ] ?? {} );

            case 'Number':
                return Inputs.NumberInput( setting[ 1 ], [ setting[ 0 ] ], objInput, setting[ 4 ] ?? {} );

            case 'Boolean':
                return Inputs.BooleanInput( setting[ 1 ], [ setting[ 0 ] ], objInput, setting[ 4 ] ?? {} );

            case 'Color':
                return Inputs.ColorInput( setting[ 1 ], [ setting[ 0 ] ], objInput, setting[ 4 ] ?? {} );

            case 'Select':
                return Inputs.SelectInput( setting[ 1 ], [ setting[ 0 ] ], objInput, setting[ 4 ] ?? {} );

            case 'Position':
                return Inputs.PositionInput( setting[ 1 ], [ setting[ 0 ] ], objInput, setting[ 4 ] ?? {} );

            case 'Array':
                return Inputs.ArrayInput( setting[ 1 ], [ setting[ 0 ] ], objInput, setting[ 4 ] ?? {} );
                
            default:
                break;

        }

    }

    render() {

        const content = [];

        let objInput = {
            id: 0,
            data: structuredClone( this.props.listNeed ),
            updateData: this.updateSettings
        };
  
          
        for ( let setting of this.props.needDetails.settings.data ) {

            content.push( this.getInput( setting, objInput ) );
            // needSettings[ setting[ 0 ] ] = setting[ 3 ];

        }
        // const cardList = [];

        // for ( let need of this.props.listNeed ) {

        //     cardList.push(
                
        //         <div className={ 'card card_data' }
        //         key={need.id}>
        //             <div className='card_content'>
        //                 <div className='card_content_left'>
        //                     { this.getNameInput( need.id ) }
        //                     { this.getTypeInput( need.id ) }
        //                 </div>
        //                 <div className='card_content_right'>
        //                     <button className='icon_button' id='delete' onClick={this.handleDelete}></button>
        //                 </div>
        //             </div>
        //         </div>
        //     );
    
        // }
    
        return (
            <div className='section_body' id='settingsT_section'>
                <div className='settingsT_group'>
                    {content}
                </div>
            </div>
        )

    }

}


export default SettingsSectionT;