import React from 'react';
import './cameraMoveSection.scss';
import MoveCard from './MoveCard';

class CameraMoveSection extends React.Component {

  constructor( props ) {

    super( props );

    let cameraMoves = [];

    if ( this.props.json.video.cameraMoves ) {

      cameraMoves = structuredClone( this.props.json.video.cameraMoves );
      cameraMoves.map( ( cMove ) => {
        cMove.id = Math.floor( Math.random() * Date.now() )
      } );

    }

    this.state = {
      cameraMoves: cameraMoves
    }

  }

  getObjectById( id ) {

    for ( let i = 0; i < this.state.cameraMoves.length; i++ ) {

      if ( this.state.cameraMoves[ i ].id === id ) return { index: i, object: this.state.cameraMoves[ i ] }

    }

  }

  updateMove = ( id, newMove ) => {

    this.state.cameraMoves[ this.getObjectById( id ).index ] = newMove;
    this.setState( { ...this.state, cameraMoves: this.state.cameraMoves } );
    let updatedCameraMoves = structuredClone(  this.state.cameraMoves );
    updatedCameraMoves.map( (cMove ) => { delete cMove.id } );
    this.props.json.video.cameraMoves = updatedCameraMoves;
    this.props.updateJSON( this.props.json );

  }

  deleteMove = ( id ) => {
    
    this.state.cameraMoves.splice( this.getObjectById( id ).index, 1 );
    this.setState( { ...this.state, cameraMoves: this.state.cameraMoves } );
    let updatedCameraMoves = structuredClone(  this.state.cameraMoves );
    updatedCameraMoves.map( (cMove ) => { delete cMove.id } );
    this.props.json.video.cameraMoves = updatedCameraMoves;
    this.props.updateJSON( this.props.json );

  }

  addMove = () => {
    
    this.props.json.video.cameraMoves = this.props.json.video.cameraMoves ?? [];

    let newMove = {
      type: "Jump",
      timestep: 0
    };

    this.props.json.video.cameraMoves.push( newMove );
    this.props.updateJSON( this.props.json );
    this.state.cameraMoves.push( { ...newMove, id: Math.floor( Math.random() * Date.now() ) } );
    this.setState( { ...this.state, cameraMoves: this.state.cameraMoves } );

  }

  render() {

    const cardList = [];

    for ( let data of this.state.cameraMoves ) {

      cardList.push(
        <MoveCard
          data={ data }
          updateData={ this.updateMove }
          deleteData={ this.deleteMove }
          id={ data.id }
          key={ data.id }
        />
      );

    }

    return (
      <div className={'section_body ' + ( this.props.isCollapsed ? 'collapse' : 'expand') } id='camera_move_section'>
        <div className='card_group'>
          { cardList }
        </div>
        <div className='section_footer'>
          <button className='floating_button' id='add' onClick={ this.addMove }>+</button>
        </div>
      </div>
    )

  }

}


export default CameraMoveSection;