import React from 'react';
import './videoSection.scss';
import * as Inputs from '../cardInputs.js';

class VideoSection extends React.Component {

  // eslint-disable-next-line no-useless-constructor
  constructor( props ) {
    super( props );   
  };

  setFpsValue = (e) => {
      this.props.json.video.settings.fps = parseInt(e.target.value);
      this.props.updateJSON( this.props.json );
  };
  
  setDurationValue = () => {
    let m = parseInt(document.getElementById("m").value);
    let s = parseInt(document.getElementById('s').value);
    
      if (isNaN(m) && isNaN(s)){
        m=0;
        s=0
      }else if(isNaN(m)){
        m = 0
      }else if(isNaN(s)){
        s = 0
      };

    let convertToMs = (m, s) =>(m*60 +s)*1000;
    let result = convertToMs(m,s)
   
    this.props.json.video.settings.duration = result;
      if(result === 'number'){
        return this.props.updateJSON( this.props.json );
      };
  };

  dispatchPreviousValue = () => {
    let ms = this.props.json.video.settings.duration;
    let minutes = Math.floor(ms / 60000);
    let seconds = Math.round((ms % 60000) / 1000);
      return {
        sec: seconds.toString(),
        min: minutes.toString()
      };
  };

  updateData = ( id, newData ) => {

    if ( this.props.videoOrImage === 'video' ) {

      newData.duration = newData.duration * 1000;
      newData.fps = parseInt( newData.fps );
      this.props.json.video.settings = newData;

    } else {

      this.props.json.image.settings = newData;

    }

    this.props.updateJSON( this.props.json );

  }

  updateTime = ( id, newData ) => {

    this.props.json.video.settings.duration = newData.duration * 1000;
    this.props.updateJSON( this.props.json );

  }

  render() {

    let content = [];

    let objData = {
      id: 0,
      data: null,
      updateData: this.updateData
    };

    if ( this.props.videoOrImage === 'video' ) {
      
      objData.data = structuredClone( this.props.json.video.settings );
      objData.data.duration = objData.data.duration / 1000;

      let objTime = {
        id: 0,
        data: objData.data,
        updateData: this.updateTime
      };

      content.push( Inputs.NumberInput( "Dure en secondes", [ 'duration' ], objTime, { "min": 0, "max": 120 } ) );
      content.push( Inputs.SelectInput( "Images par secondes", [ 'fps' ], objData, { "type": "String", "options": [ 30, 60 ] } ) );

    } else {
      
      objData.data = structuredClone( this.props.json.image.settings );

      content.push( Inputs.PositionInput( "Position de la camera", [ 'cameraPosition' ], objData, { "lat": true, "long": true, "alt": true } ) );
      content.push( Inputs.PositionInput( "Cible de la camera", [ 'lookAt' ], objData, { "lat": true, "long": true, "alt": true } ) );

    }

    content.push( Inputs.NumberInput( "Hauteur de l'image", [ 'height' ], objData, { "min": 1, "max": 3840 } ) );
    content.push( Inputs.NumberInput( "Largeur de l'image", [ 'width' ], objData, { "min": 1, "max": 3840 } ) );

    return (
      <div className={'section_body ' + ( this.props.isCollapsed ? 'collapse' : 'expand') } id='video_section'>
        <div className='video-card'>
          {content}
        </div>
          {/* <div className="video-custom-input">
            <h4>Frames per second </h4>
              <label htmlFor="fps-select"> Fps:</label>
                <select value={this.props.json.video.settings.fps} id="video-fps-select" onChange={this.setFpsValue}>
                    <option value="30">30</option>
                    <option value="45">45</option>
                    <option value="60">60</option>
                </select>
          </div>
          <div className="video-custom-input">
              <h4>Duration</h4>
                <label htmlFor="minutes">In minutes:</label>
                <input className="video-input" defaultValue={this.dispatchPreviousValue().min} type="number" 
                       id="m" name="minutes" min="0" max="60" onChange={this.setDurationValue}/> 
                <label  className="video-scd-input" htmlFor="seconds">In seconds:</label>
                <input className="video-input" defaultValue={this.dispatchPreviousValue().sec} type="number" 
                       id="s" name="seconds" min="0" max="60" onChange={this.setDurationValue}/>                
          </div>                 */}
      </div>
    );
  };
};

export default VideoSection;