import React from 'react';
import ActionCard from './actionCard.js';
import objDesc from '../objectsDescription.json';

class ActionSection extends React.Component {

  constructor( props ) {

    super( props );

    let actionList = [];

    if ( this.props.json.scene.actions ) {

        actionList = structuredClone( this.props.json.scene.actions );
        actionList.map( ( action ) => {
            action.id = Math.floor( Math.random() * Date.now() )
        } );

    }

    this.state = {
        actionList: actionList
    }

  }

  getActionById( id ) {

    for ( let i = 0; i < this.state.actionList.length; i++ ) {

        if ( this.state.actionList[ i ].id === id ) return { index: i, action: this.state.actionList[ i ] }

    }

  }

  updateData = ( id, newData ) => {

    this.state.actionList[ this.getActionById( id ).index ] = newData;
    this.setState( { ...this.state, actionList: this.state.actionList } );
    let updatedActionList = structuredClone( this.state.actionList );
    updatedActionList.map( (data ) => { delete data.id } );
    this.props.json.scene.actions = updatedActionList;
    this.props.updateJSON( this.props.json );

  }

  deleteData = ( id ) => {
    
    this.state.actionList.splice( this.getActionById( id ).index, 1 );
    this.setState( { ...this.state, actionList: this.state.actionList } );
    let updatedActionList = structuredClone( this.state.actionList );
    updatedActionList.map( (data ) => { delete data.id } );
    this.props.json.scene.actions = updatedActionList;
    this.props.updateJSON( this.props.json );

  }

  addData = () => {
    
    if ( this.props.json.scene.objects.length > 0 ) {

      this.props.json.scene.actions = this.props.json.scene.actions ?? [];
      
      let type = Object.keys( objDesc[ this.props.json.scene.objects[ 0 ].information.type ].actions )[ 0 ];

      let newAction = {
        timestep: 0,
        name: this.props.json.scene.objects[ 0 ].name,
        type: type,
        settings: {}
      };
      
      this.props.json.scene.actions.push( newAction );
      this.props.updateJSON( this.props.json );
      this.state.actionList.push( { ...newAction, id: Math.floor( Math.random() * Date.now() ) } );
      this.setState( { ...this.state, actionList: this.state.actionList } );
      console.log( this.state.actionList );

    }

  }

  render() {

    const cardList = [];

    for ( let data of this.state.actionList ) {

      cardList.push(
        <ActionCard
          data={data}
          objectNameList={ this.props.json.scene.objects }
          updateData={ this.updateData }
          deleteData={ this.deleteData }
          id={data.id}
          key={data.id}
        />
      );

    }

    return (
      <div className={'section_body ' + ( this.props.isCollapsed ? 'collapse' : 'expand') } id='action_section'>
        <div className='card_group'>
          {cardList}
        </div>
        <div className='section_footer'>
          <button className='floating_button' id='add' onClick={ this.addData }>+</button>
        </div>
      </div>
    )

  }

}


export default ActionSection;