function handleChangeType( event, props ) {
    
    props.data.information.type = event.target.value;
    props.updateData( props.id, props.data );

}

export function TypeInput( props ) {

    return (
        <label id="type">
            Type :
            <select name="type" value={ props.data.information.type } onChange={ (event) => handleChangeType( event, props ) }>
            <option value="Density_Map_2D">Density Map 2D</option>
            <option value="Density_Map_3D">Density Map 3D</option>
            </select>
        </label>
    );

}